@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&family=Zeyada&display=swap";
* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(to bottom right, #353536, #d8d5d5);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
  overflow: hidden;
}

.leaflet-popup-content-wrapper, .leaflet-popup-tip {
  color: #7fffd4;
  background-color: #2c2b2b;
}

.hidden {
  display: none;
}

.pop-up {
  color: #0ff;
  word-wrap: break-word;
  background-color: #000;
  border-left: 5px solid #5858af;
  border-radius: 5px;
}

#map-container {
  border: 1px solid #000;
  width: 95vw;
  height: 75vh;
  position: relative;
  top: -75px;
}

.modal {
  z-index: 9999;
  opacity: .8;
  background-color: #131212;
  border: 1px solid #000;
  border-radius: 15px;
  width: 350px;
  height: 250px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.closeModal {
  color: #000;
  background-color: #3b3b3b;
  border-radius: 15px;
  position: absolute;
  left: 325px;
}

.comment {
  background-color: #beb9b9;
  width: 250px;
  height: 150px;
  position: absolute;
  top: 50px;
  left: 50px;
}

.comment-box {
  word-wrap: break-word;
  text-align: center;
  width: 65vw;
  height: 15vh;
  position: absolute;
  bottom: 10px;
}

.sign-out {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #131212;
  border-radius: 5px;
  width: 150px;
  height: 30px;
  margin-top: 10px;
  padding-top: 4px;
  display: block;
  position: relative;
  top: -100px;
  box-shadow: 0 25px 30px -5px #16181b;
}

.sign-out:active {
  background-color: #4d504d;
  transform: translateY(4px);
  box-shadow: 0 5px #666;
}

