@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&family=Zeyada&display=swap');

* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(
    to bottom right,
    rgb(53, 53, 54),
    rgb(216, 213, 213)
  );
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow: hidden;
  margin: 0;
}
.leaflet-popup-content-wrapper,
.leaflet-popup-tip {
  color: aquamarine;
  background-color: #2c2b2b;
}
.hidden {
  display: none;
}
.pop-up {
  border-radius: 5px;
  color: aqua;
  word-wrap: break-word;
  background-color: black;
  border-left: 5px solid rgb(88, 88, 175);
}
#map-container {
  top: -75px;
  position: relative;
  height: 75vh;
  width: 95vw;
  border: 1px solid black;
}

.modal {
  position: absolute;
  height: 250px;
  width: 350px;
  border: 1px solid black;
  border-radius: 15px;
  background-color: #131212;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  opacity: 0.8;
}
.closeModal {
  position: absolute;
  left: 325px;
  color: black;
  border-radius: 15px;
  background-color: #3b3b3b;
}
.comment {
  position: absolute;
  height: 150;
  width: 250;
  background-color: rgb(190, 185, 185);
  top: 50px;
  left: 50px;
}
.comment-box {
  position: absolute;
  word-wrap: break-word;

  width: 65vw;
  height: 15vh;
  bottom: 10px;
  text-align: center;
}
.sign-out {
  width: 150px;
  height: 30px;
  color: #fff;
  background-color: #131212;
  border-radius: 5px;
  text-align: center;
  margin-top: 10px;
  padding-top: 4px;
  box-shadow: rgb(22, 24, 27) 0px 25px 30px -5px;
  display: block;

  top: -100px;
  position: relative;
  cursor: pointer;
}
.sign-out:active {
  background-color: #4d504d;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
